import { DataStore } from './../../models/data-store';
import { VeiculoService } from './../../providers/api/veiculo.service';
import { TranslateService } from "@ngx-translate/core";
import { ElementRef } from "@angular/core";
import { ViewChild } from "@angular/core";
import { Mensagem } from "./../../models/mensagem";
import { Component } from "@angular/core";
import { IonicPage, NavController, NavParams, Content, LoadingController, ToastController } from "ionic-angular";

/**
 * Generated class for the MensagemPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-mensagem",
  templateUrl: "mensagem.html"
})
export class MensagemPage {
  @ViewChild(Content) content: Content;
  public mensagens: Mensagem[];
  public veiculo: DataStore;
  @ViewChild("chat_input") messageInput: ElementRef;
  editorMsg = "";
  sendMessageString: string;
  waitString: string;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public translateService: TranslateService,
    public veiculoService:VeiculoService,
    public loadingCtrl: LoadingController,
    public toastCtrl: ToastController,
  ) {
    this.translateService.get(["SEND_MESSAGE", "WAIT"]).subscribe(values => {
      this.sendMessageString = values["SEND_MESSAGE"];
      this.waitString = values["WAIT"];
    });
    this.mensagens = this.navParams.get("mensagens");
    this.veiculo = this.navParams.get("veiculo");
    this.scrollToBottom();
  }

  ionViewDidLoad() {
    console.log("ionViewDidLoad MensagemPage");
  }

  scrollToBottom() {
    setTimeout(() => {
      if (this.content.scrollToBottom) {
        this.content.scrollToBottom();
      }
    }, 400);
  }

  sendMsg() {
    if (!this.editorMsg.trim()) return;

    const msg = {
      veiculoId: this.veiculo.veiculoId,
      transmissao: 'ANY',
      msgTxt: this.editorMsg.trim()
    };

    let loading = this.loadingCtrl.create({
      content: this.waitString
    });
    loading.present();
    this.veiculoService
      .enviarMensagem(msg)
      .subscribe((data:any) => {
        this.atualizar()
      }, error => {
        let toast = this.toastCtrl.create({
          message: 'Falha: Tente novamente!',
          duration: 3000,
          position: 'top'
        });
        toast.present();
        loading.dismiss();
      }, () => {
        this.editorMsg = '';
        loading.dismiss();
      });
  }

  atualizar() {
    let loading = this.loadingCtrl.create({
      content: this.waitString
    });
    loading.present();
    this.veiculoService
      .getMensagem( this.veiculo.veiculoId)
      .subscribe(
        (res: any) => {
          this.mensagens = res.mensagens;
        },
        error => {
          let toast = this.toastCtrl.create({
            message: 'Falha: Tente novamente!',
            duration: 3000,
            position: 'top'
          });
          toast.present();
          loading.dismiss();
        },
        () => {
          loading.dismiss();
        }
      );
  }

  onFocus() {
    this.content.resize();
    this.scrollToBottom();
  }

  doRefresh(refresher) {
      this.veiculoService
      .getMensagem(this.veiculo.veiculoId)
      .subscribe(
        (res: any) => {
          this.mensagens = res.mensagens;
        },
        error => {
          let toast = this.toastCtrl.create({
            message: 'Falha: Tente novamente!',
            duration: 3000,
            position: 'top'
          });
          toast.present();
          refresher.complete();
        },
        () => {
          refresher.complete();
        }
      );
  }
}



